___CSS_LOADER_EXPORT___.push([module.id, ".front{margin-left:20%}.slide-fade-enter-active{transition:all .1s ease}.slide-fade-leave-active{transition:all .4s cubic-bezier(1,.5,.8,1)}.slide-fade-enter,.slide-fade-leave-to{opacity:0;transform:translateX(10px)}.card{text-align:center;will-change:transform}.flip-enter-active{transition:all .4s ease}.flip-leave-active{display:none}.flip-enter-from,.flip-leave-to{opacity:0;transform:rotateY(180deg)}", "",{"version":3,"sources":["webpack://./resources/js/views/Words.vue"],"names":[],"mappings":"AAoPA,OACI,eACJ,CACA,yBACI,uBACJ,CACA,yBACI,0CACJ,CACA,uCAGI,SAAU,CADV,0BAEJ,CACA,MACI,iBAAkB,CAClB,qBACJ,CACA,mBACI,uBACJ,CAEA,mBACI,YACJ,CAEA,gCAEI,SAAU,CADV,yBAGJ","sourcesContent":["<template>\n    <div class=\"container mx-auto px-6 py-8\">\n        <loader :loading=\"isLoading\"></loader>\n        <h3 class=\"text-3xl font-semibold text-gray-700\">{{ i18n.t('languageList.header') }}</h3>\n        <div class=\"mt-4 mb-3\">\n            <h4 class=\"text-gray-700\">{{ i18n.t('languageList.intro') }}</h4>\n            <div class=\"max-w-sm mt-6 overflow-hidden bg-white rounded shadow-lg\" v-if=\"word\">\n                <transition name=\"slide-fade\" mode=\"out-in\">\n                    <div :key=\"word.id\">\n                        <div class=\"flex\">\n                            <div class=\"flex-auto\">\n                                <div :title=\"i18n.t('from_sample')\" class=\"ml-3 text-blue-800\" v-if=\"word.from_sample\">\n                                    <svg class=\"w-6 h-6\" fill=\"none\" stroke=\"currentColor\" viewBox=\"0 0 24 24\" xmlns=\"http://www.w3.org/2000/svg\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" stroke-width=\"2\" d=\"M3 21v-4m0 0V5a2 2 0 012-2h6.5l1 1H21l-3 6 3 6h-8.5l-1-1H5a2 2 0 00-2 2zm9-13.5V9\"></path></svg>\n                                </div>\n                                <div :title=\"i18n.t('from_user')\" class=\"ml-3 text-blue-800\" v-else-if=\"word.shared_by\">\n                                    <svg class=\"w-6 h-6\" fill=\"currentColor\" viewBox=\"0 0 20 20\" xmlns=\"http://www.w3.org/2000/svg\"><path d=\"M13 6a3 3 0 11-6 0 3 3 0 016 0zM18 8a2 2 0 11-4 0 2 2 0 014 0zM14 15a4 4 0 00-8 0v3h8v-3zM6 8a2 2 0 11-4 0 2 2 0 014 0zM16 18v-3a5.972 5.972 0 00-.75-2.906A3.005 3.005 0 0119 15v3h-3zM4.75 12.094A5.973 5.973 0 004 15v3H1v-3a3 3 0 013.75-2.906z\"></path></svg>\n                                </div>\n                            </div>\n                            <div class=\"flex-auto\">\n                                <div class=\"ml-3 text-blue-800\">\n                                    <p>#{{ word.id }}</p>\n                                </div>\n                            </div>\n                            <div class=\"flex-auto\">\n                                <div class=\"ml-3 text-blue-800\">\n                                    <p v-if=\"words\">{{ current + 1 }} / {{ words.length }}</p>\n                                </div>\n                            </div>\n                            <div class=\"flex-auto text-blue-800\">\n                                <share-component :id=\"word.id\"></share-component>\n                            </div>\n                        </div>\n                        <transition name=\"flip\">\n                            <div class=\"card\" :key=\"getWordKeyTranslation\">\n                                <div class=\"px-6 py-4 mt-2\">\n                                    <div class=\"mb-2 text-2xl font-bold text-gray-900 text-left\">{{ word[getWordKeyTranslation] }}</div>\n                                </div>\n                                <card-buttons :word=\"word\"></card-buttons>\n                            </div>\n                        </transition>\n\n                    </div>\n                </transition>\n            </div>\n            <div v-else>\n                <no-words-found :target=\"target\"></no-words-found>\n            </div>\n        </div>\n        <footer class=\"text-gray-600 body-font\" v-if=\"word\">\n            <div class=\"border-t border-gray-200\">\n                <div class=\"container px-5 py-8 flex flex-wrap mx-auto items-center\">\n                    <div class=\"flex md:flex-nowrap flex-wrap justify-center items-end md:justify-start\">\n                        <el-button\n                            :label=\"word && word.done_at ? i18n.t('languageList.unknow') : i18n.t('languageList.know')\"\n                            @click=\"markKnown\"\n                            add-css=\"inline-flex text-white bg-red-500 border-0 py-3 px-6 focus:outline-none hover:bg-indigo-600 rounded mt-3\">\n                        </el-button>\n                        <el-button\n                            :label=\"i18n.t('languageList.show')\"\n                            @click=\"showTranslation\"\n                            add-css=\"inline-flex text-white bg-yellow-500 border-0 py-3 px-6 focus:outline-none hover:bg-indigo-600 rounded mt-3\">\n                        </el-button>\n                        <el-button\n                            :label=\"i18n.t('languageList.prev')\"\n                            @click=\"showPrev\"\n                            :disabled=\"prev === null\"\n                            add-css=\"inline-flex text-white bg-blue-600 border-0 py-3 px-6 focus:outline-none hover:bg-indigo-600 rounded mt-3\">\n                        </el-button>\n                        <el-button\n                            :label=\"i18n.t('languageList.next')\"\n                            @click=\"showNext\"\n                            add-css=\"inline-flex text-white bg-indigo-500 border-0 py-3 px-6 focus:outline-none hover:bg-indigo-600 rounded mt-3\">\n                        </el-button>\n                    </div>\n                    <span class=\"inline-flex lg:ml-auto lg:mt-0 mt-6 w-full justify-center md:justify-start md:w-auto\">\n      </span>\n                </div>\n            </div>\n            <div class=\"bg-gray-100\">\n                <div class=\"container mx-auto py-4 px-5 flex flex-wrap flex-col sm:flex-row\">\n                    <p class=\"text-gray-500 text-sm text-center sm:text-left\">© 2021 EasyWords —\n                        <a href=\"https://sergeyem.ru\" class=\"text-gray-600 ml-1\" target=\"_blank\" rel=\"noopener noreferrer\">Sergey Emelyanov</a>\n                    </p>\n                    <span class=\"sm:ml-auto sm:mt-0 mt-2 sm:w-auto w-full sm:text-left text-center text-gray-500 text-sm\">Learn words faster!</span>\n                </div>\n            </div>\n        </footer>\n    </div>\n</template>\n\n<script lang=\"ts\">\nimport {computed, defineComponent, onMounted, ref, reactive} from 'vue'\nimport {useSettingsStore} from \"../store/settings\";\nimport {useI18n} from 'vue-i18n';\nimport Loader from '../components/shared/Loader.component.vue'\nimport {useWordsStore} from \"../store/words\";\nimport ElButton from \"../components/primitives/buttons/ElButton.vue\"\nimport { WordInterface } from \"../models/words/Word.interface\";\nimport VueFlip from 'vue-flip';\nimport CardButtons from '../components/words/CardButtons.vue'\nimport NoWordsFound from '../components/words/NoWordsFound.vue'\nimport ShareComponent from \"../components/words/ShareComponent.vue\"\nimport getNextWord from \"../services/getNextWord\";\nimport {WordsMutationType} from \"@/models/store/words/WordsMutationType\";\nimport nextWord = WordsMutationType.nextWord;\n\nexport default defineComponent({\n    name: \"Words\",\n    components: {\n        Loader, ElButton, VueFlip, CardButtons, NoWordsFound, ShareComponent\n    },\n    props: {\n        parent: {\n            type: String,\n            required: true\n        },\n        target: {\n            type: String,\n            required: true\n        },\n        rev: {\n            type: String,\n            required: true\n        }\n    },\n    setup(props) {\n        const settingsStore = useSettingsStore()\n        const wordsStore = useWordsStore()\n        const showTranslate = ref<boolean>(false);\n        const flipped = ref<boolean>(false)\n        const index = ref<number>(1);\n        let prev = ref<number|null>(null)\n        let word = ref<WordInterface|null>(null)\n\n        function showNext() {\n            setNulls();\n            prev.value = current.value;\n            index.value++;\n            generateWord();\n        }\n\n        const current = computed<number>(() => {\n            if (!word || !word.value) {\n                return 0;\n            }\n            return wordsStore.state.words.findIndex(wordFound => {\n                if (!word.value) {\n                    return false\n                }\n                return wordFound.original === word.value.original\n            })\n        })\n\n        function setNulls() {\n            flipped.value = false\n            showTranslate.value = false\n        }\n\n        function showPrev() {\n            setNulls();\n            if (prev.value) {\n                word.value = words.value[prev.value];\n                index.value--;\n            }\n            prev.value = null;\n        }\n\n        const settings = computed(() => {\n            return settingsStore.state.settings\n        })\n\n        const words = computed(() => {\n            return wordsStore.state.words\n        })\n\n        const isLoading = computed(() => {\n            return wordsStore.state.loading\n        })\n\n        function generateWord() {\n            word.value = getNextWord(current.value, prev.value, index.value)\n        }\n\n        const getWordKeyTranslation = computed(() => {\n            if (showTranslate.value) {\n                if (props.rev === 'right') {\n                    return 'translated'\n                } else {\n                    return 'original'\n                }\n            } else {\n                if (props.rev === 'right') {\n                    return 'original'\n                } else {\n                    return 'translated'\n                }\n            }\n        })\n\n        const i18n = useI18n()\n\n        function receiveWords() {\n            wordsStore.actionAsync('loadWords', {\n                language: props.target\n            }).then(words => word.value = words[0])\n        }\n\n        function showTranslation() {\n            if (!word.value) {\n                return;\n            }\n            flipped.value = !flipped.value\n            showTranslate.value = !showTranslate.value\n            if (showTranslate.value) {\n                wordsStore.action('markViewed', word.value.id)\n            }\n        }\n\n        function markKnown() {\n            setNulls();\n            if (!word.value) {\n                return;\n            }\n            wordsStore.action('markKnown', {\n                id: word.value.id,\n                value: word.value.done_at ? 0 : 1\n            })\n            prev.value = null;\n            showNext();\n        }\n\n        onMounted(() => {\n            receiveWords();\n        });\n\n        return {\n            i18n, isLoading, word, showTranslate, getWordKeyTranslation, showTranslation, flipped, showNext,\n            markKnown, index, words, showPrev, prev, current\n        }\n    }\n})\n</script>\n\n<style>\n.front {\n    margin-left: 20%;\n}\n.slide-fade-enter-active {\n    transition: all .1s ease;\n}\n.slide-fade-leave-active {\n    transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);\n}\n.slide-fade-enter, .slide-fade-leave-to\n    /* .slide-fade-leave-active for <2.1.8 */ {\n    transform: translateX(10px);\n    opacity: 0;\n}\n.card {\n    text-align: center;\n    will-change: transform;\n}\n.flip-enter-active {\n    transition: all 0.4s ease;\n}\n\n.flip-leave-active {\n    display: none;\n}\n\n.flip-enter-from, .flip-leave-to {\n    transform: rotateY(180deg);\n    opacity: 0;\n\n}\n</style>\n"],"sourceRoot":""}]);
